.home__body {
    flex: 1;
    display: flex;
    margin-top: 200px;
    flex-direction: column;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}

.search__input {
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    height: 25px;
    padding: 10px 15px;
    border-radius: 999px;
    width: 79vw;
    margin: 0 auto;
    margin-top: 10px;
    max-width: 520px;
}

.search__input__inline {
    align-items: left;
    border: 1px solid lightgray;
    height: 22px;
    padding: 10px 15px;
    border-radius: 999px;
    width: 79vw;
    margin: 0 auto;
    margin-top: 10px;
    max-width: 520px;
}

.search__input:hover {
    box-shadow: 0 1px 6px rgba(32,33,36,.28);
}

.search__input__inline:hover {
    box-shadow: 0 1px 6px rgba(32,33,36,.28);
}

.search__buttons button {
    margin: 5px;
}

.search__buttons__inline button {
    margin: 5px;
    display: inline
}

/* Autosuggest */

.react-autosuggest__container {
position: relative;
}

.react-autosuggest__input {
width: 240px;
height: 15px;
padding: 10px 10px;
font-family: Helvetica, sans-serif;
font-weight: 300;
font-size: 16px;
border: none;
border-radius: 4px;
}

.react-autosuggest__input--focused {
outline: none;
}

.react-autosuggest__input--open {
border-bottom-left-radius: 0;
border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
display: none;
}

.react-autosuggest__suggestions-container--open {
display: block;
position: absolute;
top: 40px;
width: 79vw;
max-width: 490px;
border: 1px solid #aaa;
background-color: #fff;
font-family: Helvetica, sans-serif;
font-weight: 300;
font-size: 16px;
text-align: left;
border-bottom-left-radius: 4px;
border-bottom-right-radius: 4px;
z-index: 2;
}

.react-autosuggest__suggestions-list {
margin: 0;
padding: 0;
list-style-type: none;
}

.react-autosuggest__suggestion {
cursor: pointer;
padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
background-color: #ddd;
}
